<template>
  <div>
    <div class="action-btn">
      <i class="iconfont icon icon-caidan" @click="show=true">
      </i>
    </div>
    <yd-actionsheet :items="types" v-model="show"></yd-actionsheet>
  </div>

</template>

<script type="text/ecmascript-6">
  export default{
    name: 'filter-sheet',
    props:['filters'],
    data(){
      return {
        show: false,
        types: [
          {
            label: '所有课程',
            method: () => {
              this.$router.push('/searchtype')
            }
          },
          {
            label: '直播课程',
            method: () => {
              this.$router.push('/searchtype')
            }
          },
          {
            label: '录播课程',
            method: () => {
              this.$router.push('/searchtype')
            }
          }
        ]
      }
    },
    created(){
      //this.show = this.value
    },
    /*watch: {
      show(n, o){
        if (!n) {
          this.$emit('input', false)
        }
      }
    }*/
  }
</script>

<style scoped lang="less">
  @import "../style/config.less";
  .action-btn{
    padding: 10px 20px;
    text-align: right;
    .iconfont{
      font-size: 20px;
      color:@skin-color;
    }
  }
</style>



