import Navbar from './Navbar.vue'
import SearchBox from './SearchBox.vue'
import FilterSheet from './FilterSheet.vue'

const components=[
  Navbar,
  SearchBox,
  FilterSheet
]
export default {
  install(Vue){
    components.map(component => {
      Vue.component(component.name, component);
    });
  }
}



