<template>
  <section class="search-box">
    <a href="#/search"><i class="icon iconfont icon-sousuo"></i></a>
    <input type="text" class="" placeholder="集智AI学院" @click="show=true">
    <yd-popup class="search-page" v-model="show" position="right" width="100%" height="100%">
      <i class="iconfont icon-arrow-left back" @click="show=false"></i>
      <div class="search-box inner">
        <a href="#/search"><i class="icon iconfont icon-sousuo"></i></a>
        <input type="text" class="" placeholder="集智AI学院">
      </div>
      <div class="search-panel">
        <p class="name">搜索记录</p>
        <p>
          <router-link to="/search">强化学习</router-link>
          <router-link to="/search">强化学习</router-link>
        </p>
        <p class="name">热门搜索</p>
        <p>
          <router-link to="/search">强化学习</router-link>
          <router-link to="/search">强化学习</router-link>
        </p>
      </div>
    </yd-popup>
    </div>
  </section>
</template>

<script>
    export default{
      name:'search-box',
      data(){
        return {
          show:false,
        }
      }
    }
</script>

<style lang="less">
  .search-box{
    display: flex;
    margin: 10px;
    border:1px solid #555;
    border-radius:15px;
    align-items: center;
    padding: 5px;
    &>input{
      flex:1;
      border:none;
      outline: none;
      margin-left: 10px;
    }
    &.inner{
    background-color: #fff;
    border-radius: 5px;
     }
  }
  .search-page{

    .back{
      position: absolute;
      top:20px;
      left:20px;
      color:#fff;
    }
  }
  .search-panel{
    margin: 10px;
    color:#fff;
  .name{
    margin-bottom: 10px;
  }
    a{
      display: inline-block;
      padding: 5px;
      background-color: #f1f1f1;
      border-radius: 3px;
      color:#000;
      margin:0 10px 10px 0;
    }
  }
  .m-popup-mask{
    background-color: rgba(0,0,0,0.6);
  }
  .m-popup,.m-popup .popup-content{
    background-color: transparent;
  }
  .popup-content{
    padding: 80px 30px 30px;
  }
</style>



